.Partner{
    height: min-content;
    display: flex;
    align-items: center;
    padding-top: 150px;
    padding-bottom: 0px;
    background: white;
    /* width: calc(100vw - 150px);
  margin-left: 150px; */

}
.Partner-box{
    display: grid;
    grid-template-columns: 1.6fr 1fr;
    height: 500px;
}

.Partner-photos{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5px;
}

.Partner-photos .investor{
    background: url(../../Images/contractor.jpg) no-repeat center center/cover;
    grid-row: 1 / span 2;
}
.Partner-photos .realtor{
    
    background: url(../../Images/realtor.jpg) no-repeat center center/cover;
}
.Partner-photos .contractor{
 
    background: url(../../Images/invest-6.jpg) no-repeat center center/cover;
}

.Partner-content{
    display: flex;
    flex-flow: column;
    align-items: flex-end;
    justify-content: center;
    
}

.Partner-content .sit{
  padding-left: 100px;
}

.Partner-content .sit p{
    color: var(--text-color);
    padding: 20px 0px 30px 0px;
}
.P-photo{
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    cursor: pointer;
}
.P-photo:hover p{
    opacity: 1;
}
/* .P-photo::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
} */

.P-photo p{
    background: linear-gradient(0deg, rgba(7, 7, 7, 0.507) 0%, rgba(0, 0, 0, 0.377) 35%, rgba(0, 0, 0, 0) 100%);
    color: white;
    padding: 10px 20px;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: bold;
    transition: all .3s ease-in-out;
    opacity: 0;
}